import React, { Component } from 'react'
import Intro from 'components/About/Intro'
import styled from 'styled-components'

const StyledAbout = styled.section`
  margin-top: 70px;
`
export default class About extends Component {
  constructor(props) {
    super(props)
  }
  render() {
    return (
      <StyledAbout>
        <Intro />
        <p>개인 소개</p>
        <p>회사에서 했던 프로젝트</p>
        <p>관심 있는 것</p>
        <p>개인적으로 해본 것</p>
      </StyledAbout>
    )
  }
}



